/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&family=Great+Vibes&display=swap');

body { margin: 0; overflow: hidden; background: #000; font-family: 'Poppins', sans-serif; }

/* SHARED SCREEN CONTAINER */
.screen {
  position: absolute; top: 0; left: 0; width: 100%; height: 100vh;
  display: flex; flex-direction: column; align-items: center; overflow: hidden;
}

/* STAGE 1: COUNTDOWN */
.countdown-screen {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  justify-content: center; color: white;
}
@keyframes gradientBG { 0% { background-position: 0% 50%; } 50% { background-position: 100% 50%; } 100% { background-position: 0% 50%; } }
.timer { font-size: 3.5rem; font-weight: bold; }

/* STAGE 2: CAKE SCREEN */
.cake-screen {
  background: radial-gradient(circle, #ffeaa7 0%, #fab1a0 100%);
  position: relative;
}

/* --- IMAGE ASSETS --- */
.ribbon-banner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; 
  height: 250px; 
  /* background-image: url('./ribbons.png');  */
  background-repeat: repeat-x; 
  background-size: contain; 
  background-position: top center; 
  z-index: 0;
}

